import clsx from "clsx";
import React, { Component } from "react";
// import Swiper core and required modules
import SwiperCore, {
  A11y,
  Autoplay,
  Navigation,
  Pagination,
  Scrollbar,
} from "swiper";
import { Swiper, SwiperSlide } from "swiper/react";
// Import Swiper styles
import "swiper/swiper-bundle.min.css";
import "swiper/swiper.min.css";
import DefaultImage from "../../assets/images/common/default.png";
import CustomImage from "../UI/CustomImage/CustomImage";
import "./Slider.css";

// install Swiper modules
SwiperCore.use([Navigation, Pagination, Scrollbar, A11y, Autoplay]);

class Slider extends Component {
  state = {
    params: {
      spaceBetween: 10,
      // navigation: this.props.navigation ? true : false,
      navigation: {
        nextEl: ".arrow-right",
        prevEl: ".arrow-left",
      },
      pagination: this.props.pagination ? { clickable: true } : false,
      loop: true,
      autoplay: { delay: 5000 },
      // Responsive breakpoints
      breakpoints: {
        100: {
          slidesPerView:
            this.props.params.slidesPerView < 1
              ? this.props.params.slidesPerView
              : 1,

          centerInsufficientSlides: true,
        },
        400: {
          slidesPerView:
            this.props.params.slidesPerView < 2
              ? this.props.params.slidesPerView
              : 1,

          centerInsufficientSlides: true,
        },
        768: {
          slidesPerView:
            this.props.params.slidesPerView < 3
              ? this.props.params.slidesPerView
              : 3,
          centeredSlides: true,
        },
        // when window width is >= 1000px
        1000: {
          slidesPerView: this.props.params.slidesPerView,
          spaceBetween: 20,
        },
      },
      ...this.props.params,
    },
  };

  updateSlidesPerView() {}

  componentDidMount() {
    document.addEventListener("resize", this.updateSlidesPerView);
  }

  render() {
    let swiperSlides = null;
    if (this.props.images) {
      swiperSlides =
        this.props.images.length > 0 ? (
          this.props.images.map((image, i) => {
            return (
              <SwiperSlide key={i}>
                <CustomImage
                  noLazyLoad
                  style={{ width: this.props.imageWidth + "%", height: "60vh" }}
                  src={image}
                  alt="Banner"
                />
              </SwiperSlide>
            );
          })
        ) : (
          <CustomImage
            noLazyLoad
            style={{ width: this.props.imageWidth + "%", height: "60vh" }}
            src={DefaultImage}
            useCustomSrc
            alt="Banner"
          />
        );
    }

    if (this.props.components) {
      swiperSlides = this.props.components.map((component, i) => {
        return <SwiperSlide key={i}>{component}</SwiperSlide>;
      });
    }

    return (
      <div
        className={
          this.props.innerNavigation
            ? clsx("SwiperCover", "InnerNavigationCover")
            : "SwiperCover"
        }
      >
        <div className="arrow-left">
          <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24">
            <path d="M15.293 3.293 6.586 12l8.707 8.707 1.414-1.414L9.414 12l7.293-7.293-1.414-1.414z" />
          </svg>
        </div>
        <Swiper
          className={this.props.innerNavigation ? "InnerNavigation" : null}
          {...this.state.params}
        >
          {swiperSlides}
        </Swiper>
        <div className="arrow-right">
          <i class="fas fa-chevron-right">
            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24">
              <path d="M7.293 4.707 14.586 12l-7.293 7.293 1.414 1.414L17.414 12 8.707 3.293 7.293 4.707z" />
            </svg>
          </i>
        </div>
      </div>
    );
  }
}
export default Slider;
